import React, { useEffect, useState } from 'react';

import './App.css';
import Topbar from './Components/Topbar/Topbar';
import Sidebar from './Components/Sidebar/sidebar';
import Card from './Components/Card/card';
import LoadingAnim from './Components/LoadingAnim/LoadingAnim';

interface ApiResult {
    title: string,
    author: string,
    _id: string,
    chapter: number,
    partA: string,
    partB: string,
    partC: string
}

function App() {
    const [apiData, setApiData] = useState<ApiResult[]>([]);
    const [skipState, setSkipState] = useState(0);

    function loadMore() {
        fetch(`https://container-service-1.01c6s0c46j1b0.us-east-1.cs.amazonlightsail.com/admin/loadCards?skip=${skipState}`)
            .then(blob => blob.json())
            .then(data => {
                setApiData(apiData.concat(data));
                setSkipState(skipState + 5);
            });
    }

    useEffect(loadMore, []);

    if (!localStorage.getItem("hasSeenAlert")) {
        alert("The CCCP exists as an online resource for concept card help. Copying cards verbatim, in addition to being morally wrong, violates MCPS policies on academic integrity. All graded work must be original.");
        localStorage.setItem("hasSeenAlert", "true");
    }

    return <>
        <Topbar title="The CCCP"/>
        <div className="featured-card-wrapper">
            {
                apiData
                ? <><div className="solely-card-wrapper"> {apiData.map(card => {
                    return <Card
                        concept={card.title}
                        author={card.author}
                        favorited={false}
                        partA={card.partA}
                        partB={card.partB}
                        partC={card.partC}
                        _id={card._id}
                        key={card._id}
                    />
                })}
                </div>
                <button onClick={loadMore} className="more-button">Load More</button>
                </>
                : <LoadingAnim/>
                
            }
        </div>
        <Sidebar/>
    </>;
}

export default App;
